import React from 'react';
import Layout from '../components/layout';

import './rsvp.css';

class RSVP extends React.Component {
  state = {
    rsvp: null,
    numAdults: 0,
    numChildren: 0,
    firstName: null,
    lastName: null,
  };

  handleRSVPChange(e) {
    console.log(e);
  }

  render() {
    return (
      <Layout container="true" title="RSVP">
        <form>
          <div className="formGroup">
            <div className="inputGroup">
              <label className="required" htmlFor="firstName">First Name</label>
              <input onChange={(e) => this.setState({ firstName: e.target.value })} name="firstName" id="firstName" type="text" required />
            </div>
            <div className="inputGroup">
              <label className="required" htmlFor="lastName">Last Name</label>
              <input name="lastName" id="lastName" type="text" required />
            </div>
          </div>

          <div className="formGroup">
            <div className="inputGroup">
              <label className="required" htmlFor="rsvp">RSVP</label>

              <div className="radioGroup">
                <label htmlFor="yes">Accepts</label>
                <input onChange={() => this.setState({rsvp: 'yes'})} name="rsvp" id="yes" value="Accepts" type="radio" required />
              </div>

              <div className="radioGroup">
                <label htmlFor="no">Regrets</label>
                <input onChange={() => this.setState({rsvp: 'no'})} name="rsvp" id="no" value="Regrets" type="radio" required />
              </div>
            </div>
          </div>

          {
            this.state.rsvp == 'yes' ? 
            <div className="formGroup">
              <div className="inputGroup">
                <label className="required" htmlFor="numAdults">Number of Adults Attending</label>
                <input name="numAdults" id="numAdults" type="number" pattern="\d*" required />
              </div>
              <div className="inputGroup">
                <label className="required" htmlFor="numChildren">Number of Children Attending</label>
                <input name="numChildren" id="numChildren" type="number" pattern="\d*" required />
              </div>
            </div>
            :
            ''
          }
        </form>
      </Layout>
    );
  }
}

export default RSVP;
